.statusBar{
    max-width: 0px;
    height: 3px;
    width: 100%;
    background-color: #7dc13d;
    position: fixed;
    top:0px;
    transition: 1s;
}
.statusBarRun{
    animation: expand 3s infinite;
    animation-iteration-count: 1;
}

.MainCont{
    opacity: 0;
    transform: scale(.9);
    transition: .3s;
    margin-top: 100px;
}
.inputText{
    outline: none;
    border: none;
    padding-left: 5px;
    border-bottom: 2px solid;
    border-radius: 5px;
    font-size: 18px;
    color: #2c2f36;
    background-color: #00000000;
    margin-top: 20px;
    width: 250px;
    box-sizing: border-box;
    display: inline-block;
    text-align:left;
    padding-left: 10px;
}
.Links{
    color: #475ed1;
    display: block;
}
.inputText::placeholder {
    color: #505768;
    font-size: inherit;
  }
.flex{
    display: flex;
    align-items: center;
    justify-content: end;
}
.DashCards {
    background-color: #e5ebfc7c;
    border-radius: 20px;
    box-shadow: 0 8px 13px 5px rgba(86,86,86,.819);
    height: auto;
    margin: 50px auto auto;
    min-height: 150px;
    padding: 20px;
    transition: .3s;
    width: 35%;
}
.DashCards:hover{
    transform: scale(1.05);
}
 
div {
    display: block;
}
body {
    font-family: Tilt Neon,cursive;
    margin: 0;
}

h1{
    text-align: center;
}
 
.forms input[type=otp]{
    border-radius: 0px;
    width: 30%;
  }
  .forms{
    display: block;
    margin: auto;
    text-align: center;
    width:300px;
    padding: 20px;
    background-color: rgba(184, 178, 178, 0.535);
    border-radius: 10px;
  }
  .forms input[type=text],.forms input[type=password],.forms input[type=email] {
    text-align: left;
    width: 90%;
    font-size: 17px;
    margin-top: 30px;
  }
  .submitBtn{
    font-size: 90px;
  }

  .smLoader {
    display: none;
    margin-left: 10px;
    width: 15px;
    height: 15px;
    border-radius: 50%;
    border: 3px dotted rgb(26, 24, 24) ;
    border-right-color:rgba(255, 255, 255, 0);
    background-color: none;
    animation: loader .6s infinite linear;
}
.submitBtn{
    display: flex;
    margin: auto;
    height: 32px;
    padding: 5px 10px;
    justify-content: center;
    align-items: center;
    border: 1px solid black;
    font-size: 20px;
    border-radius: 5px;
    margin-top: 15px;
    margin-bottom: 10px;
}
#googleLogin {
    align-items: center;
    background-color: #e7e7e700;
    border: 1px solid #ffffff00;
    border-radius: 5px;
    display: flex;
    margin: auto;
    padding-right: 10px;
    transition: .5s;
    width: -webkit-max-content;
    width: max-content;
}
.MainCont{
    opacity: 0;
    transform: scale(.9);
    transition: 0.3s;
}
.showMainCont{
    animation: showall .5s infinite forwards;
    animation-iteration-count: 1;
}
@keyframes showall {
    0%{
        opacity: 0.5;
    }100%{
        opacity: 1;
        transform: scale(1);
    }
}

#googleLogin:hover{
    background-color: #91909054;
    border: 1px solid;
    transform: scale(1.02);
}
.visible{
    display: block;
}
@keyframes loader {
    0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
       
      }
  }
  @media only screen and (max-width: 400px) {
    .forms{
        width: 80%;
    }
    
  }
  /* There is only one style sfor logout component so included here  */
  #LogoutMsg{
    text-align: center;
    margin-top: 250px;
  }